import React, { useState } from "react";
import { DragDropContext, Draggable, Droppable } from "react-beautiful-dnd";
import shuffle from "lodash.shuffle";
import { Link } from "react-router-dom";
import {v4 as uuid} from 'uuid'
const itemsFromBackend = [
  { id: '0', content: "A customer comes to the office to acquire a vehicle" ,status:false},
  { id: '1', content: "The clerk locates the vehicle reservation contract by means of the reservation number and/or customer name" ,status:false},
  { id: '2', content: "The customer signs the contract and the clerk gives the keys to the vehicle" ,status:false},
  { id: '3', content: "The clerk then marks the contract active by entering the vehicle release date onto the vehicle reservation contract",status:false },
  { id: '4', content: "The use case terminates at this point",status:false }
];

const columnsFromBackend = {
  [uuid()]: {
    name: "Jumbled",
    items: shuffle(itemsFromBackend)
  }, [uuid()]: {
    name: "Ordered",
    items: []
  }
};


const onDragEnd = (result, columns, setColumns) => {
  
  if (!result.destination) return;
  const { source, destination } = result;  

  if (source.droppableId !== destination.droppableId) {
    const sourceColumn = columns[source.droppableId];
    const destColumn = columns[destination.droppableId];
    const sourceItems = [...sourceColumn.items];
    const destItems = [...destColumn.items];
    const [removed] = sourceItems.splice(source.index, 1);
    destItems.splice(destination.index, 0, removed);
    setColumns({
      ...columns,
      [source.droppableId]: {
        ...sourceColumn,
        items: sourceItems
      },
      [destination.droppableId]: {
        ...destColumn,
        items: destItems
      }
    });
    console.dir(columns);
  } else {
    const column = columns[source.droppableId];
    const copiedItems = [...column.items];
    const [removed] = copiedItems.splice(source.index, 1);
    copiedItems.splice(destination.index, 0, removed);    
    setColumns({
        ...columns,
        [source.droppableId]: {
          ...column,
          items: copiedItems        
        }
      });   
   }
};



export default function Usecase() {
    const [columns, setColumns] = useState(columnsFromBackend);      
    const[validCount,setValidCount]= useState(0);
    const[invalidCount,setInvalidCount]= useState(0);
    
    const verify = (col, b,setColumns) => {    
  
      var validCount=0;
      var invalidCount=0;
      Object.entries(col).map(([columnId, column], index) => {    
        if(column.name === 'Ordered'){              
            var uc_list = column.items;
            for(var i =0;i<uc_list.length;i++){                 
                var placeId = parseInt(i);
                var recId = parseInt(uc_list[i].id);              
                if(placeId === recId){                                   
                  column.items[i].status = false;   
                  validCount++;
                  setValidCount(validCount);                   
                }
                else{                     
                  column.items[i].status = true; 
                  invalidCount++;     
                  setInvalidCount(invalidCount)              
                 }              
            }
          } 
        });      
        setColumns({
          ...col,          
        });
      
        
    }
    return (
        <div className="container mx-auto" >
               <div className="row-span-2 md:row-span-4" style={{ display: "flex", justifyContent: "center"}} >
                <DragDropContext  onDragEnd={result => onDragEnd(result, columns, setColumns)} >
                  {Object.entries(columns).map(([columnId, column], index) => {
                    return (
                      <div
                        style={{
                          display: "flex",
                          flexDirection: "column",
                          alignItems: "center"   ,
                          marginTop:"20px"   ,    
                          fontSize:34,
                          fontFamily : "font-mono"                        
                                                  
                        }}
                        key={columnId}
                      >
                        <div className={'text-slate-500 hover:text-blue-600] font-mono font-bold'}>Usecase:{column.name}</div>
                        <div style={{ margin: 8}}>
                          <Droppable droppableId={columnId} key={columnId}>
                            {(provided, snapshot) => {
                              return (
                                <div
                                  {...provided.droppableProps}
                                  ref={provided.innerRef}
                                  style={{
                                    background: snapshot.isDraggingOver
                                      ? "lightblue"
                                      : "lightgrey",
                                    padding: 4,
                                    width: 650,
                                    minHeight: 500,                                  
                                    fontSize:26,
                                    fontFamily : "font-mono" 
                                  
                                  }}                       
                                >
                                  {column.items.map((item, index) => {
                                    return (
                                      <Draggable
                                        key={item.id}                              
                                        draggableId={item.id}
                                        index={index}                              
                                      >
                                        {(provided, snapshot) => {
                                          return (
                                            <div
                                              id={columnId}                                                                                                      
                                              ref={provided.innerRef}
                                              {...provided.draggableProps}
                                              {...provided.dragHandleProps}
                                              style={{
                                                userSelect: "none",
                                                padding: 16,
                                                margin: "0 0 8px 0",
                                                minHeight: "50px",
                                                backgroundColor: snapshot.isDragging
                                                  ? "#263B4A"
                                                  : "#8ab1cb",                                     
                                                ...provided.draggableProps.style ,                                         
                                                color:item.status?"#a61b13":"black",
                                              }} 
                                            >
                                              <div>
                                                {item.content}
                                              
                                              </div>
                                            </div>
                                          );
                                        }}
                                      </Draggable>
                                    );
                                  })}
                                  {provided.placeholder}
                                </div>
                              );
                            }}
                          </Droppable>
                        </div>                                       
                      </div>              
                    );
                  })}
                </DragDropContext>                
                </div>         
                <div className="flex justify-center items-center">
                  <div>              
                      <button onClick={(event) => verify(columns, event,setColumns)} className={`cursor-pointer bg-gray-200 text-xl text-black font-mono font-bold active:bg-gray-300 p-2 rounded-full border shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150`} type="button" >VALIDATE </button>
                  </div>   
                  <div className="shrink">
                  { 
                    validCount === 5 ? <> <div className={`text-[#047857] font-mono font-bold `}>Usecases are in CORRECT order...   
                      <Link className={`cursor-pointer bg-gray-200 text-xl text-black font-mono font-bold active:bg-gray-300 p-2 rounded-full border shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150`} to="/nounphrase">NEXT</Link></div>
                    </> 
                    : <>
                    {invalidCount === 0  
                      ? <div className={`text-slate-500 hover:text-blue-600] font-mono font-bold`}>DRAG Usecases in Correct order from Left and DROP in Right section* </div>
                      : <div className={`text-[#b91c1c] font-mono font-bold`}>Usecases are NOT in CORRECT Order,Try again... </div>
                    }
                   </> 
                  }                            
                   
                  </div>
  
              </div>    
      </div>

    )
}